import { Box, Typography } from '@mui/material'
import React from 'react'
import textStyle from '../helpers/textStyle'

const PrivacyPolicy = () => {
    return (
        <div style={{ width: '90%', margin: 'auto', marginTop: '20px' }}>
            <Typography style={{ ...textStyle, fontSize: 36, fontWeight: 700 }}>PRIVACY POLICY</Typography>
            <Typography style={textStyle} my={1}>
                Bloom By Khushbu (us, we, or our) operates https://bloombykhushbu.co.in (the Site). This page informs you of our policies regarding the collection, use, and disclosure of Personal Information we receive from users of the Site. We use your Personal Information only for providing and improving the Site. By using the Site, you agree to the collection and use of information in accordance with this policy.
            </Typography>
            <Typography style={{ ...textStyle, fontSize: 25, fontWeight: 700 }} my={2}>Information Collection and Use</Typography>
            <Typography style={textStyle} my={1}>
                While using our Site, we may ask you to provide us with certain personally identifiable information that can be used to contact or identify you. Personally identifiable information may include but is not limited to your name, email id, and/or your mobile number (Personal Information).
            </Typography>
            <Typography style={{ ...textStyle, fontSize: 25, fontWeight: 700 }} my={2}>Log Data</Typography>
            <Typography style={textStyle} my={1}>
                Like most site operators, we collect information that your browser sends whenever you visit our Site ("Log Data). This Log Data may include information such as your computer's Internet Protocol ("IP") address, browser type, browser version, the pages of our Site that you visit, the time and date of your visit, the time spent on those pages, and other statistics. In addition, we may use third party services such as Google Analytics that collect, monitor, and analyze this data.
            </Typography>
            <Typography style={{ ...textStyle, fontSize: 25, fontWeight: 700 }} my={2}>Cookies</Typography>
            <Typography style={textStyle} my={1}>
                Cookies are files with a small amount of data, which may include an anonymous unique identifier. Cookies are sent to your browser from a website and stored on your computer's hard drive. Like many sites, we use "cookies" to collect information. You can instruct your browser to refuse all cookies or to indicate when a cookie is being sent. However, if you do not accept cookies, you may not be able to use some portions of our Site.
            </Typography>
            <Typography style={{ ...textStyle, fontSize: 25, fontWeight: 700 }} my={2}>Security</Typography>
            <Typography style={textStyle} my={1}>
                The security of your Personal Information is important to us, but remember that no method of transmission over the Internet, or method of electronic storage, is 100% secure. While we strive to use commercially acceptable means to protect your Personal Information, we cannot guarantee its absolute security.
            </Typography>
            <Typography style={{ ...textStyle, fontSize: 25, fontWeight: 700 }} my={2}>Changes To This Privacy Policy</Typography>
            <Typography style={textStyle} my={1}>
                This Privacy Policy is effective as of (2nd of September 2021) and will remain in effect except with respect to any changes in its provisions in the future, which will be in effect immediately after being posted on this page. We reserve the right to update or change our Privacy Policy at any time and you should check this Privacy Policy periodically. Your continued use of the Service after we post any modifications to the Privacy Policy on this page will constitute your acknowledgment of the modifications and your consent to abide and be bound by the modified Privacy Policy.
            </Typography>
            <Typography style={textStyle} my={1}>
                If we make any material changes to this Privacy Policy, we will notify you either through the email address you have provided us or by placing a prominent notice on our website. This Privacy Policy is effective as of (1st of September 2021) and will remain in effect except with respect to any changes in its provisions in the future, which will be in effect immediately after being posted on this page.
            </Typography>
            <Typography style={textStyle} my={1}>
                We reserve the right to update or change our Privacy Policy at any time and you should check this Privacy Policy periodically. Your continued use of the Service after we post any modifications to the Privacy Policy on this page will constitute your acknowledgment of the modifications and your consent to abide and be bound by the modified Privacy Policy. If we make any material changes to this Privacy Policy, we will notify you either through the email address you have provided us or by placing a prominent notice on our website.
            </Typography>
        </div>
    )
}

export default PrivacyPolicy
